.footer {
	width: 100%;
	min-height: $height_footer;
	flex: 0 0 auto;
	padding: 0 $gl_indent 13rem;

	font-size: 1rem;

	background-color: #061312;

	will-change: transform;
	overflow: hidden;
}

.footer_top {
	@extend %global_flex_block_row_nowrap_flex-start;
	position: relative;

	padding: 7rem 0 4.4rem;

	&:before, &:after {
		content: '';
		position: absolute;
		top: 0;
		left: 50%;
		width: 100vw;
		margin-left: -50vw;
	}

	&:before {
		height: 100%;
		background-color: rgba(255, 255, 255, 0.01);
		z-index: -1;
	}

	&:after {
		top: 100%;
		height: 1px;
		border: 1px solid rgba(255, 255, 255, 0.05);
	}
}

.logo {
	&.footer_mod {
		display: block;
		width: 14.4rem;
		height: 2.8rem;
	}
}

.logo_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: contain;
	object-position: center;
}

// bottom

.footer_bottom {
	@extend %global_flex_block_row_wrap_flex-start;

	padding-top: 6rem;

	@include bp($point_3) {
		margin-bottom: -4rem;
	}
}

.footer_col {
	width: 25rem;

	&:first-child {
		margin-right: auto;
	}

	@include bp($point_2, min) {
		&:last-child {
			width: 19rem;
		}
	}

	@include bp($point_3) {
		&:first-child {
			width: 100%;
		}

		width: 33.33%;
		padding-bottom: 4rem;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		text-align: center;
	}
}

.footer_title {
	margin-bottom: 3rem;

	font-size: 1.8rem;
	font-weight: 500;
	color: $white;
}

.footer_text {
	margin-bottom: 3.4rem;

	font-size: 1.5rem;
	line-height: 1.5;
	color: $white;
	opacity: .5;
}

.footer_link {
	font-size: 1.6rem;
	font-weight: 500;
	color: $accent_color;
	text-decoration: underline;

	&:hover, &:focus {
		text-decoration: none;
	}
}

.footer_list_item {
	& + & {
		margin-top: 1.6rem;
	}
}

.footer_list_link {
	font-size: 1.5rem;
	color: $white;
	opacity: .5;

	&:hover, &:focus {
		opacity: 1;
	}
}