// form cols

.form_cols {
	@extend %global_flex_block_row_wrap_flex-start;
}

.form_wrap {
	flex-grow: 1;
	max-width: 50rem;
	margin-right: auto;

	@include bp($point_3) {
		max-width: 100%;
		margin-bottom: 4rem;
	}
}

.form_list {
	@extend %global_flex_block_row_wrap_flex-start;

	margin: 0 -2rem -2rem 0;

	.form_field {
		margin-bottom: 0;
		padding: 0 2rem 2rem 0;
	}
}

.form_title {
	margin-bottom: 4rem;

	font-size: 3.6rem;
	font-weight: 700;

	@include bp($point_4 - 1) {
		margin-bottom: 2rem;
		font-size: 2.8rem;
	}
}

.form_back {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	margin-bottom: 3rem;

	font-size: 1.5rem;
	font-weight: 500;
	color: #80858A;
	transition: color .3s;

	&:hover, &:focus {
		color: $black;
		text-decoration: none;
	}
}

.form_field_label {
	display: block;
	margin: 0 0 1rem;

	font-size: 1.5rem;
	font-weight: 500;
}

.form_field_wrap {
	position: relative;
}

.form_icon_wrap {
	position: absolute;
	top: 50%;
	right: 1.6rem;

	width: 1.6rem;
	height: 1.6rem;
	margin-top: -.8rem;
	color: #999;

	&.close_mod {
		z-index: 2;
		background-color: rgba(250, 252, 252, 0.69);
	}

	&.hidden_mod {
		opacity: 0;
		pointer-events: none;
	}

	&:hover, &:focus {
		color: $black;
	}

	svg {
		fill: currentColor
	}
}

// breadcrumbs

.breadcrumbs {
	@extend %global_flex_block_row_wrap_center;

	max-width: 95rem;
	margin: 0 auto 11rem;

	counter-reset: breadcrumb;

	@include bp($point_4 - 1) {
		margin: 0 -2rem 5rem;
	}
}

.breadcrumbs_item {
	position: relative;

	padding: 0 2rem;
	width: 33.33%;

	&:before, &:after {
		content: '';
		position: absolute;
		top: 2rem;

		height: 1px;
		width: calc(50% - 4rem);

		background-color: $gray;
	}

	&:before {
		left: 0;
	}

	&:after {
		right: 0;
	}

	&:first-child {
		&:before {
			display: none;
		}
	}

	&:last-child {
		&:after {
			display: none;
		}
	}
}

.breadcrumbs_count {
	@extend %global_flex_block_row_nowrap_center_center;

	width: 3.6rem;
	height: 3.6rem;
	margin: 0 auto 2rem;

	border: 2px solid $gray;
	border-radius: 50%;

	.breadcrumbs_item.active_mod & {
		color: $accent_color;
		border-color: $accent_color;

		&:after {
			color: $accent_color;
		}
	}

	.breadcrumbs_item.prev_mod & {
		position: relative;
		border-color: $accent_color;

		&:after {
			opacity: 0;
		}

		&:before {
			content: '';
			position: absolute;
			top: -2px;
			left: -2px;
			right: -2px;
			bottom: -2px;
			border-radius: 50%;
			background: url('../../i/check_2.svg') no-repeat center center;
			background-size: contain;
			z-index: 10;
		}
	}

	&:after {
		counter-increment: breadcrumb;
		content: counter(breadcrumb);

		font-size: 1.6rem;
		font-weight: 700;
		color: $gray;
	}

}

.breadcrumbs_title {
	font-size: 1.8rem;
	font-weight: 500;
	text-align: center;

	.breadcrumbs_item.active_mod & {
		font-weight: 700;
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
	}
}


// sidebar

.form_sidebar {
	padding: 4.4rem 4rem;
	max-width: 35rem;
	margin: 1rem 6rem 0;

	background-color: rgba($accent_color, .03);
	border: 1px solid rgba($accent_color, .5);
	border-radius: 1rem;

	@include bp($point_3) {
		width: 100%;
		max-width: 100%;
		margin: 0;
	}

	@include bp($point_4 - 1) {
		padding: 3rem 2rem;
	}
}

.form_sidebar_img_wrap {
	width: 6.4rem;
	height: 6.4rem;
	margin: 0 0 4rem;
}

.form_sidebar_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: contain;
	object-position: center;
}

.form_sidebar_title {
	margin-bottom: 3.6rem;

	font-size: 2rem;
	font-weight: 500;
}

.form_sidebar_price {
	@extend %global_flex_block_row_nowrap_flex-start;

	margin-bottom: 4.6rem;

	@include bp($point_4 - 1) {
		align-items: center;
	}
}

.form_sidebar_price_currency {
	font-size: 2.4rem;
	font-weight: 500;
}

.form_sidebar_price_amount {
	font-size: 4.2rem;
	line-height: .8;
	font-weight: 700;

	@include bp($point_4 - 1) {
		font-size: 3.4rem;
	}
}

.form_sidebar_price_period {
	margin: auto 0 0 1rem;
	font-size: 1.6rem;

	@include bp($point_4 - 1) {
		margin: 0 0 0 .4rem;
	}
}

.form_sidebar_list {
	padding-top: 3.6rem;

	border-top: 1px solid rgba(#233E3C, .15);
}

.form_sidebar_list_item {
	position: relative;

	padding-left: 2.2rem;

	font-size: 1.7rem;
	color: rgba($black, .7);

	& + & {
		margin-top: 2rem;
	}

	&:after {
		content: '';
		position: absolute;
		top: .4rem;
		left: 0;
		width: 1.4rem;
		height: 1rem;
		background: url('../../i/check.svg') no-repeat center center;
		background-size: contain;
	}
}

.form_verify_link_w {
	@extend %global_flex_block_row_wrap_center;
}

.form_verify_link {
	font-weight: 500;
	font-size: 1.6rem;
	line-height: 1.5;
	text-decoration: underline;
	color: $accent_color;
	&:hover, 
	&:focus {
		text-decoration: none;
	}
}

.form_active_wrap {
	margin-top: 3rem;
}