//---------------------------------------------icons

.icon {
	&.size_mod {
		display: block;
		width: 100%;
		height: 100%;
	}

	&.slider_mod {
		font-size: 1.6rem;
		@include bp($point_4 - 1) {
			font-size: 1.2rem;
		}
	}

	&.back_mod {
		margin: -.1rem 1rem 0 0;
		font-size: 1rem;
	}

	&.forward_mod {
		font-size: 1rem;
		margin-left: 1rem;
		color: $accent_color;

		transform: rotate(180deg);
	}
}