.contact_us_img_w {
	margin-bottom: 7rem;
	width: 100%;

	@include bp($point_3) {
		max-width: 40rem;
		margin: 0 auto 4rem;
	}
}

.contact_us_img {
	width: 100%;
	height: auto;
}

.contact_us_list_icon {
	position: absolute;
	left: 0;
	top: -2px;

	height: 3rem;
	width: 3rem;

	color: $accent_color;

	@include bp($point_4 - 1) {
		top: .2rem;
		width: 2rem;
		height: 2rem;
	}
}

.contact_us_list {
	margin: 0 auto;
	max-width: 30rem;
	width: 100%;
}

.contact_us_list_item {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	position: relative;

	margin-bottom: 2.6rem;
	padding-left: 4.6rem;

	&:last-child {
		margin-bottom: 0;
	}
}

.contact_us_list_link {
	color: $black_1;
	font-weight: 500;
	font-size: 1.6rem;
	line-height: 1.5;

	&:hover,
	&:focus {
		color: $green;
		text-decoration: none;
	}
}

.contact_us_block {
	padding: 4rem 5rem 6rem;

	border: 1px solid $gray;
	border-radius: 8px;

	@include bp($point_4 - 1) {
		padding: 3rem 2rem;
	}
}