//------------------------------------------------------------layout
.header {
	position: fixed;
	top: 0;
	left: 0;

	height: $height_header;
	width: 100%;
	padding: 0 $gl_indent;

	font-size: 1rem;

	will-change: transform;

	background-color: $white;

	@include z-index(header);
}

.header_in {
	@extend %global_flex_block_row_wrap_flex-start_center;

	max-width: $page_width;
	width: 100%;
	height: 100%;
	margin: 0 auto;
}

.header_logo_w {
	width: 11.6rem;
	height: 22rem;
}

.header_logo {
	display: block;
	height: 100%;
	width: 100%;

	object-fit: contain;
}

//------------------------------------------------------------layout###



//---------------------------------------------menu
.header_menu_w {
	margin-left: auto;
}


.main_menu_link,
.header_log_in_link {
	font-weight: 500;
	font-size: 1.7rem;
	line-height: 1;
	color: rgba($black_1, .7);

	&:hover,
	&:focus,
	.main_menu_item.active_mod & {
		color: $green;
		text-decoration: none;
	}
}

.header_log_in {
	
	@include bp($point_2, min) {
		margin-right: 3rem;
	}
	@include bp($point_3) {
		margin-bottom: $gl_indent;
	}
	
	
}

.header_active_w {
	@extend %global_flex_block_row_wrap_flex-start_center;


	@include bp($point_2, min) {
		margin-left: 6rem;
	}

	@include bp($point_3) {
		flex-direction: column;
	}

}

.header_menu {
	@extend %global_flex_block_row_wrap_flex-start_center;
	@extend %transition_transform;

	@include bp($point_3) {
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100vh;
		margin: auto;
		padding: $height_header $gl_indent;

		overflow: auto;
		background: $white;
		transform: translate(0, -100%);

		@include flex_block(column, nowrap, center, center);


		body.menu_open & {
			transform: translate(0, 0);
		}
	}
}

.main_menu_list {
	@extend %global_flex_block_row_wrap_flex-start;

	@include bp($point_3) {
		width: 100%;
		flex: 0 0 auto;
		flex-direction: column;
		align-items: center;
		align-content: center;
		margin-bottom: 6rem;
	}
}

.main_menu_item {

	@include bp($point_2, min) {
		margin-right: 3rem;

		&:last-child {
			margin-right: 0;
		}
	}

	@include bp($point_3) {
		padding: 2rem 0 0;
		flex: 0 0 auto;
	}
}

//---------------------------------------------menu###

//---------------------------------------------trigger
@mixin user_menu_butt_line {
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	height: .4rem;
	margin-top: -.2rem;
	background: $black_1;
}

.menu_trigger {
	z-index: 210;
	position: absolute;
	top: 50%;
	right: $gl_indent;
	display: none; // hidden on wide screens
	height: 2.6rem;
	width: 3.8rem;
	margin-top: -1.6rem;
	text-transform: uppercase;

	color: transparent;
	cursor: pointer;

	@include bp($point_3) {
		display: block; // appear on tablets
	}

	&:before {
		content: '';
		transform: translate(0, -1rem);
		transition: all .3s ease;

		@include user_menu_butt_line;
	}

	&:after {
		content: '';
		transform: translate(0, 1rem);
		transition: all .3s ease;

		@include user_menu_butt_line;
	}

	&.active_mod {
		&:before {
			transform: (rotate(45deg) translate(0, 0));
		}

		&:after {
			transform: (rotate(-45deg) translate(0, 0));
		}
	}
}

.menu_trigger_decor {
	transition: all .3s ease;

	.active_mod & {
		opacity: 0;
		transform: translate(100%, 0);
	}

	@include user_menu_butt_line;
}

//---------------------------------------------trigger###