.icon {
	display: inline-block;
	width: 1em;
	height: 1em;
	fill: currentColor;
}

.icon-check {
	font-size:(32/10)*1rem;
	width:(32/32)*1em;
}
.icon-checked {
	font-size:(11/10)*1rem;
	width:(15/11)*1em;
}
.icon-empty {
	font-size:(45.44/10)*1rem;
	width:(45.44/45.44)*1em;
}
.icon-facebook {
	font-size:(24/10)*1rem;
	width:(14.18/24)*1em;
}
.icon-left {
	font-size:(15.46/10)*1rem;
	width:(8.77/15.46)*1em;
}
.icon-linkedin {
	font-size:(20.8/10)*1rem;
	width:(20.8/20.8)*1em;
}
.icon-right {
	font-size:(15.46/10)*1rem;
	width:(8.77/15.46)*1em;
}
.icon-twitter {
	font-size:(19.53/10)*1rem;
	width:(24/19.53)*1em;
}
