.banner {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	position: relative;

	padding: 6.2rem 6rem 7rem;
	max-width: 103rem;
	margin: 0 auto;

	border-radius: 1rem;
	overflow: hidden;
	
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #f0f6f6;

		z-index: -2;
	}

	// &:after {
	// 	content: '';
	// 	position: absolute;
	// 	top: 0;
	// 	left: 0;
	// 	width: 16rem;
	// 	height: 16rem;
	// 	margin-top: -5rem;
	// 	margin-left: -4rem;
	// 	background-color: rgba(232, 178, 96, 0.3);
	// 	border-radius: 50%;
	// 	z-index: -1;
	// }

	@include bp($point_4 - 1) {
		padding: 4rem 3rem;
		flex-wrap: wrap;
	}
}

.banner_content {
	@include bp($point_4, min) {
		margin-right: 4rem;
	}

	@include bp($point_4 - 1) {
		text-align: center;
		margin-bottom: 3rem;
	}
}

.banner_title {
	margin-bottom: 1.6rem;

	font-size: 3.2rem;
	font-weight: 700;
}

.banner_text {
	font-size: 1.7rem;
	color: rgba(21, 23, 31, 0.7);
}