.how_list {
	margin-bottom: 4rem;
}

.how_item {
	position: relative;

	margin-bottom: 2.8rem;
	padding-left: 6.8rem;

	&:last-child {
		margin-bottom: 0;
	}
}

.how_item_img_w {
	position: absolute;
	top: 0.3rem;
	left: 0;


	width: 4.4rem;
	height: 4.4rem;
}

.how_item_img {
	display: block;
	height: 100%;
	width: 100%;
}

.how_item_title {
	margin-bottom: 1rem;

	color: $black_1;
	font-weight: 700;
	font-size: 1.8rem;
	line-height: 1.11;
}

.how_item_text {
	font-size: 1.6rem;
	line-height: 1.4;
}

.how_img_block {
	@extend %global_flex_block_row_wrap_center_center;

	position: relative;


	@include bp($point_2, min) {
		height: 43rem;
	}

	width: 100%;

}

.how_img_w {

	@include bp($point_2, min) {
		position: absolute;

		margin-right: -10rem;
		width: 72rem;
		height: 47rem;
	}


	@include bp($point_3) {
		width: 100%;
	}

}