.price_list {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	margin: 0 -6.8rem -6.8rem 0;
}

.price {
	width: 33.33%;
	padding: 0 6.8rem 6.8rem 0;

	@include bp($point_3) {
		width: 100%;
	}
}

.price_in {
	@extend %global_flex_block_column_nowrap_flex-start_stretch;

	height: 100%;
	padding: 4rem;

	color: $white;
	border: 1px solid rgba(81, 173, 166, 0.08);
	border-radius: 1rem;

	&.active_mod {
		border-color: $accent_color;
		background-color: rgba(81, 173, 166, 0.03);

		.btn_v2 {
			background-color: $accent_color;
			color: $white;

			&:hover, &:focus {
				background-color: darken($accent_color, 25%);
			}
		}
	}
}

.price_img_wrap {
	width: 6.6rem;
	height: 6.6rem;
	margin-bottom: 3.4rem;
}

.price_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: contain;
	object-position: center;
}

.price_title {
	margin-bottom: 3rem;

	font-size: 2rem;
	font-weight: 500;
}

.price_value {
	@extend %global_flex_block_row_nowrap_flex-start_center;
	margin-bottom: 1.4rem;
	line-height: 1;
}

.price_value_currency {
	margin-right: .2rem;
	font-size: 2.4rem;
	font-weight: 500;
}

.price_value_data {
	font-size: 4.2rem;
	font-weight: 700;
}

.price_value_period {
	margin-left: 1rem;
	align-self: flex-end;

	font-size: 1.6rem;
	line-height: 1.8;
	opacity: .7;
}

.price_value_descr {
	margin-bottom: 1.8rem;
	min-height: 2rem;

	font-size: 1.4rem;
	color: rgba($white, .7);

	span {
		margin-right: .6rem;
		color: $accent_color;
	}
}

.price_advantages {
	padding-top: 2.4rem;
	margin-bottom: 4rem;

	border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.price_advantages_item {
	position: relative;
	padding-left: 2.4rem;

	font-size: 1.7rem;
	color: rgba($white, .7);

	& + & {
		margin-top: 1.8rem;
	}

	&:before {
		content: '';
		position: absolute;
		top: .6rem;
		left: 0;

		width: 1.4rem;
		height: 1rem;

		background: url('../../i/icons/checked.svg') no-repeat center center;
		background-size: contain;
	}
}