//---------------------------------------------fonts
$font_path: "../fonts/";
$font_1: 'DM Sans',
sans-serif;
$roboto: 'Roboto Mono',
monospace;
$dm_serif: 'DM Serif Text',
serif;
$font_size_base: 1.6rem;
//---------------------------------------------fonts###

//---------------------------------------------colors
$white: #fff;
$black: #000;

$green: #00B0A7;
$accent_color: #51ada6;
$black_1: #15171F;
$black_2: #061312;
$gray: #E4E7EB;

$gray: #D2D9D8;

$color_text_base: rgba($black_1, .7);
$color_link_base: $green;
$body_bg: $white;
//---------------------------------------------colors###

//---------------------------------------------z-index
$z-index: (popap : 9999,
	header : 100,
	footer : 100);
//---------------------------------------------z-index###

//---------------------------------------------layout
$height_footer: 10rem;
$height_header: 11rem;
$page_width: 117rem;

//---------------------------------------------layout###

//---------------------------------------------global values
$gl_indent: 2rem;
$gl_radius:.4rem;
//---------------------------------------------global values###

//---------------------------------------------media points
$point_1: 1200px;
$point_2: 1024px;
$point_3: 1023px;
$point_4: 768px;
$point_5: 640px;
$point_6: 480px;
$point_7: 375px;
$point_8: 320px;
//---------------------------------------------media points

//---------------------------------------------forms
$input_height: 4.2rem;
$input_offset: 0 2.6rem;
$input_font_size: 1.5rem;
$input_border_color: $gray;
$input_border_color_active: $green;
$input_placeholder_color: $color_text_base;
$input_text_color: $color_text_base;
$input_radius: 3px;
//---------------------------------------------forms###