.popup {
	@extend %global_flex_block_row_nowrap_flex-start;

	position: fixed;
	top: 0;
	left: 0;

	width: 100%;
	height: 100vh;
	padding: 2rem $gl_indent;

	overflow-y: auto;
	background-color: rgba($black, .6);

	z-index: 1000;
}

.popup_in {
	position: relative;

	margin: auto;
	padding: 13.4rem 8rem 8rem;
	width: 81rem;
	max-width: 100%;

	text-align: center;

	background-color: $white;
	border-radius: 1rem;

	@include bp($point_4 - 1) {
		padding: 6rem 2rem 4rem;
	}
}

.popup_close {
	position: absolute;
	top: 2.4rem;
	right: 2.4rem;

	width: 2.4rem;
	height: 2.4rem;

	color: #80858A;
	transition: all .3s;

	cursor: pointer;

	&:before, &:after {
		content: '';
		position: absolute;
		top: 0;
		left: 50%;
		height: 100%;
		width: 2px;

		background-color: currentColor;
	}

	&:before {
		transform: rotate(-45deg);
	}

	&:after {
		transform: rotate(45deg);
	}

	&:hover, &:focus {
		color: black;
	}
}

.popup_content {
	max-width: 48.2rem;
	margin: 0 auto;
}

.popup_img_wrap {
	width: 19.8rem;
	height: 16rem;

	margin: 0 auto 6.2rem;

	@include bp($point_4 - 1) {
		width: 12rem;
		height: 10rem;
		margin: 0 auto 3rem;
	}
}

.popup_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: contain;
	object-position: center;
}

.popup_title {
	margin-bottom: 1.6rem;

	font-size: 3.6rem;
	font-weight: 700;

	@include bp($point_4 - 1) {
		font-size: 2.4rem;
	}
}

.popup_text {
	margin-bottom: 4rem;

	font-size: 1.8rem;
	line-height: 1.6;

	@include bp($point_4 - 1) {
		font-size: 1.5rem;
	}
}

.bottom_text {
	margin-top: 3.6rem;
	font-size: 1.8rem;

	@include bp($point_4 - 1) {
		font-size: 1.5rem;
	}
}