//-------------------------------------------------------inputs and selects
%form_field_default {
	display: block;
	width: 100%;
	height: $input_height;
	padding: $input_offset;
	font-size: $input_font_size;
	font-family: $font_1;
	color: $input_text_color;
	box-shadow: inset 0 0 0 1px $input_border_color;
	border: none;
	border-radius: $input_radius;
	background-color: rgba(250, 252, 252, 0.69);

	&::-webkit-input-placeholder {
		color: $input_placeholder_color;
	}

	&::-moz-placeholder {
		color: $input_placeholder_color;
	}

	&:-ms-input-placeholder {
		color: $input_placeholder_color;
	}

}

%form_field_default_hover {}

%form_field_default_active {
	box-shadow: 0 0 0 1px $input_border_color_active;
}

.form_cell,
.form_field {
	width: 100%;

	&.hidden_field {
		display: none;
	}

	&.form_cell_v1_mod {}

	.form_step.v1_mod &:nth-child(2),
	.form_step.v1_mod &:nth-child(3) {
		width: 50%;
	}

	.form_step.v2_mod &:nth-child(3),
	.form_step.v2_mod &:nth-child(4) {
		width: 50%;
	}
}

.form_cell_title,
.form_field_label {

	&.hline_hide_mod,
	&.hidden_mod {
		display: none;
	}

	&.form_v1_mod {}

}

.form_field_wrap {
	position: relative;

	&.form_v1_mod {}

}

.form_field {

	&.default_mod,
	&_input,
	&_textarea {
		@extend %form_field_default !optional;

		&:focus {
			@extend %form_field_default_active !optional;
		}

		&:hover {
			@extend %form_field_default_hover !optional;
		}
	}

	&.offset_mod {
		margin-bottom: 2rem;
	}

	&.check_mod {
		width: 100% !important;

		margin: 0 0 3rem 0;
	}

	&_textarea {
		height: 14rem;
		padding-top: 1rem;
		padding-bottom: 1rem;
	}

}

//---------------------------------------------inputs and selects###

//---------------------------------------------checkboxes and radio-buttons

.form_field_text_check {
	font-size: 1.5rem;
	line-height: 2rem;
}

.form_field_radiocheck {
	display: none;
}

.form_field_text_check {
	position: relative;
	display: inline-block;
	padding: 0 0 0 3rem;

	&:before,
	&:after {
		position: absolute;
		top: -1px;
		left: 0;
		width: 2rem;
		height: 2rem;
		cursor: pointer;
	}

	&:before {
		content: '';
		border-radius: .4rem;
		border: 1.2px solid #DFDFDF;
	}

	&:after {
		// @extend %icon-check:before;
		@extend %transition_opacity;

		text-align: center;
		line-height: 2rem;
		opacity: 0;
		color: $accent_color;

		.form_field_radiocheck:checked+& {
			opacity: 1;
		}
	}

	a {
		text-decoration: underline;

		&:hover,
		&:focus {
			text-decoration: none;
		}
	}

}

