//---------------------------------------------sections
.section {
	position: relative;

	width: 100%;

	@include bp($point_2, min) {
		padding: 10rem $gl_indent;
	}

	@include bp($point_3) {
		padding: 4rem $gl_indent;
	}

	font-size: 1rem;

	&.bg_mod {
		background-color: $black_2;
	}

	&.bg_v2_mod {
		background-color: rgba($green, .03);
	}

	&.hero_mod {

		@include bp($point_2, min) {
			min-height: 73.4rem;
		}

	}

	&.supported_mod {
		overflow: hidden;
	}
}

.section_in {
	max-width: $page_width;
	width: 100%;
	margin: 0 auto;
}

.section_title {
	color: $black_1;

	font-weight: 700;


	@include bp($point_2, min) {
		font-size: 4.2rem;
	}

	@include bp($point_3) {
		font-size: 3rem;
	}


	&.offset_mod {
		margin-bottom: 11rem;
	}

	&.offset_v2_mod {
		margin-bottom: 5.4rem;
	}

	&.offset_v3_mod {
		margin-bottom: 2rem;
	}

	&.offset_v4_mod {
		margin-bottom: 1.2rem;
	}

	&.hero_mod {
		line-height: 1.17;

		@include bp($point_2, min) {
			font-size: 5.8rem;
		}

		@include bp($point_3) {
			font-size: 3.8rem;
		}

	}

	&.center_mod {
		text-align: center;
	}

	&.color_mod {
		color: $white;
	}

	span {
		font-weight: 400;
	}

}

.section_subtitle {
	margin-bottom: 1rem;

	font-size: 1.6rem;
	font-weight: 600;
	font-family: $roboto;
	color: $accent_color;
	text-align: center;
	text-transform: uppercase;
	letter-spacing: 2px;

	&.left_mod {
		text-align: left;
	}
}

.section_descr {
	font-size: 1.7rem;
	line-height: 1.41;

	&.size_mod {
		font-size: 2rem;
	}

	&.limit_mod {
		max-width: 64rem;
		margin: 0 auto
	}

	&.center_mod {
		text-align: center;
	}

	&.offset_mod {
		margin-bottom: 4rem;
	}

	&.offset_2_mod {
		margin-bottom: 8rem;
	}
}

.section_row {
	@extend %global_flex_block_row_wrap_flex-start_center;

	&.align_mod {
		align-items: stretch;
	}


	@include bp($point_2, min) {
		margin-right: -3rem;

		&.indent_mod {
			margin-right: -20rem;
		}

		&.hero_mod {
			margin-right: -14rem;
		}
	}

	@include bp($point_3) {
		flex-direction: column;
		margin-bottom: -3rem;

		.section.supported_mod & {
			flex-direction: column-reverse;
		}
	}

}

.section_col {
	position: relative;

	@include bp($point_2, min) {
		padding-right: 3rem;

		.section_row.indent_mod & {
			padding-right: 20rem;
		}

		&.v1_mod {
			width: 42%;
		}

		&.v2_mod {
			width: 58%;
		}

		&.v3_mod {
			width: 46%;
		}

		&.v4_mod {
			width: 54%;
		}
	}

	@include bp($point_3) {
		padding-bottom: 3rem;
		width: 100%;
	}


}

.section_col_in {
	position: relative;
	width: 100%;

	&.how_img_mod {
		@extend %global_flex_block_row_wrap_center_center;
	}

	&.indent_mod {
		padding-top: 4rem;
	}
}

.section_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: contain;
}

.section_head {
	width: 100%;

	&.center_mod {
		text-align: center;
	}

	&.offset_mod {
		margin-bottom: 12rem;

		@include bp($point_3) {
			margin-bottom: 6rem;
		}
	}

	&.offset_v2_mod {
		margin-bottom: 6rem;

		@include bp($point_4 - 1) {
			margin-bottom: 2rem;
		}
	}

}