.btn_base {
	@extend %green_btn_1;
	@extend %btn_size_1;
}

.btn_v1 {
	@extend %green_btn_1;
	@extend %btn_size_3;
}

.btn_v2 {
	@extend %green_btn_2;
	@extend %btn_size_1;
}

.btn_v3 {
	@extend %green_btn_1;
	@extend %btn_size_2;
}

.btn_v3 {
	@extend %green_btn_1;
	@extend %btn_size_3;
}

.full_btn {
	width: 100%;
}

.btn_wrap {
	&.end_mod {
		margin-top: auto;
	}

	&.offset_side_mod {
		margin-left: auto;

		@include bp($point_4 - 1) {
			margin: 0 auto;
		}
	}

	&.offset_mod {
		margin-bottom: 3.6rem;
	}


}

// decor

.decor {
	position: absolute;

	svg {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: contain;
		object-position: center;
	}

	&.v1_mod {
		bottom: 0;
		left: 0;
		width: 28.4rem;
		height: 28.4rem;
		margin-left: -14.2rem;
		margin-bottom: -14.2rem;
	}

	&.v2_mod {
		bottom: 4rem;
		left: 20rem;

		width: 3rem;
		height: 3rem;
	}

	&.v3_mod {
		top: 17rem;
		left: 7rem;

		width: 9.2rem;
		height: 11.6rem;
	}

	&.v4_mod {
		bottom: -4.6rem;
		right: 9rem;

		width: 4rem;
		height: 4rem;
	}

	&.v5_mod {
		top: 0;
		right: 0;
		z-index: -1;

		width: 10.2rem;
		height: 10.6rem;
	}

	&.v6_mod {
		bottom: 0;
		left: 0;
		z-index: -1;

		width: 18.8rem;
		height: 18.8rem;
	}

	&.v7_mod {
		right: 11.8rem;
		bottom: -11rem;

		width: 19rem;
		height: 18rem;

		z-index: -1;
	}

	&.v8_mod {
		top: 0;
		right: 0;
		width: 28.4rem;
		height: 28.4rem;
		margin-top: -14.2rem;
		margin-right: -14.2rem;
	}

	&.v9_mod {
		top: 9rem;
		left: 9rem;
		width: 7.2rem;
		height: 7.2rem;
	}

	&.v10_mod {
		left: 50%;
		top: 100%;
		width: 3.6rem;
		height: 3.6rem;
		margin-left: 35rem;
		margin-top: -8rem;
	}

	&.v11_mod {
		position: absolute;
		top: 0;
		left: 0;
		width: 16rem;
		height: 16rem;
		margin-top: -5rem;
		margin-left: -4rem;
		background-color: rgba(232, 178, 96, 0.3);
		border-radius: 50%;
		// z-index: -1;
	}

	&.v12_mod {
		position: absolute;
		top: 27rem;
    left: 40%;

		width: 18.4rem;
		height: 4.2rem;
	}

	&.v13_mod {
		position: absolute;
    top: 40rem;
    left: 8rem;

		width: 18.4rem;
		height: 4.2rem;

	}

	&.v14_mod {
		position: absolute;
		top: 4rem;
		right: 10rem;
		width: 3rem;
		height: 3rem;
	}
}

.decor_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: contain;
	object-position: center;
}

// social

.social_list {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	&.footer_mod {
		margin-left: auto;
	}
}

.social_item {
	&+& {
		margin-left: 3rem;
	}
}

.social_link {
	display: block;
	width: 2.4rem;
	height: 2.4rem;
	color: $white;
	opacity: .2;

	transition: all .3s;

	&:hover,
	&:focus {
		opacity: 1;
		color: $accent_color;
	}
}

.link_base {
	font-size: 1.6rem;
	text-decoration: underline;
	font-weight: 500;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}