@mixin st_btn() {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	text-align: center;
	white-space: nowrap;

	cursor: pointer;

	&:hover,
	&:focus {
		text-decoration: none;
	}

}

//color extends
%green_btn_1 {
	font-weight: 500;

	color: $white;
	background: $color_link_base;
	transition: .3s ease;

	@include st_btn();

	&:hover {
		background: darken($color_link_base, 10%);
	}
}

%green_btn_2 {
	font-weight: 500;

	color: $accent_color;
	border: 1px solid $accent_color;
	background: transparent;
	transition: .3s ease;

	@include st_btn();

	&:hover {
		background-color: $accent_color;
		color: $white;
	}
}



//size extends
%btn_size_1 {
	height: 4.2rem;
	padding: 0 2rem;

	line-height: 4.2rem;
	font-size: 1.6rem;

	border-radius: $gl_radius;
}

%btn_size_2 {
	height: 4.8rem;
	padding: 0 2.6rem;

	line-height: 4.8rem;
	font-size: 1.6rem;

	border-radius: $gl_radius;
}

%btn_size_3 {
	height: 4.4rem;
	padding: 0 2.6rem;
	min-width: 15.2rem;
	padding: 0 2.6rem;

	line-height: 4.4rem;
	font-size: 1.6rem;

	border-radius: $gl_radius;
}