.features_list {
	@extend %global_flex_block_row_wrap_center;
	margin: 0 -2rem -5rem 0;
}

.feature {
	width: 25%;
	padding: 0 2rem 5rem 0;

	@include bp($point_3) {
		width: 50%;
	}

	@include bp($point_5) {
		width: 100%;
		max-width: 40rem;
	}
}

.feature_img_wrap {
	margin-bottom: 3.4rem;

	height: 14.8rem;
	width: 100%;
}

.feature_img {
	display: block;
	height: 100%;
	width: 100%;
	object-fit: contain;
	object-position: center;
}

.feature_title {
	margin-bottom: 1.6rem;

	font-size: 2rem;
	font-weight: 700;
	color: $black_1;
	text-align: center;
}

.feature_text {
	font-size: 1.6rem;
	color: rgba(21, 23, 31, 0.7);
	text-align: center;
}