.supported_img_w {
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;

	width: 100%;
	height: 53.6rem;
}

.supported_pictures_block_w {
	position: relative;

	max-width: 46rem;
	width: 100%;

	overflow-y: hidden;

	@include bp($point_2, min) {
		margin: -10rem 0 -10rem 5rem;
	}

	@include bp($point_3) {
		margin: 0 auto -4rem;
	}

	@include bp($point_4, min) {
		height: 53.6rem;
	}

	@include bp($point_4 - 1) {
		height: 29rem;

		font-size: .6rem;
	}

}

.supported_pictures_block {
	position: absolute;
	bottom: 5.4rem;

	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: flex-start;
	align-content: flex-start;
	width: (16em + 16em + 12em);

	@include bp($point_4 - 1) {
		left: calc((100% - (16em + 16em + 12em)) / 2);
	}
	// height: 100%;
}

.pointer_none{
	pointer-events: none;
	will-change: transform;
}

.supported_pictures_item {
	position: relative;

	// width: 33.33%;
	width: 16em;
	height: 16em;
	padding-bottom: 4em;
	padding-right: 4em;

	&:first-child {
		top: 12em;
	}

	&:nth-child(3n + 4) {
		top: 12em;
	}

	&:nth-child(3n+2) {
		top: 4.8em;
	}

	&:nth-child(3n) {
		width: 12em;
		padding-right: 0;
	}
}

.supported_picture {
	@extend %global_flex_block_row_wrap_center_center;

	position: absolute;

	width: 12em;
	height: 12em;
	padding: 1em;
	opacity: 0;
	top: 0;
	left: 0;

	background-color: $white;
	box-shadow: 0.8em 0.8em 2em 0.1em rgba(53, 113, 108, 0.15);
	border-radius: 1.2rem;
}

.supported_picture_img_w {
	height: 8em;
	width: 8em;
}

.supported_picture_img {
	height: 100%;
	width: 100%;

	object-fit: contain;
}
