.testimonials_wrap {
	position: relative;

	padding: 10rem;
	margin: -10rem; 
	overflow: hidden; 

	.slick-list {
		overflow: visible;
	}

	&:before , &:after {
		content: '';
		position: absolute;
		top: 0;

		width: calc(50% - 24rem);
		height: 100%;

		z-index: 2;
	}

	&:before {
		left: 0;
		background: linear-gradient(to right, $white 0%, transparent 100%);
	}

	&:after {
		right: 0;
		background: linear-gradient(to left, $white 0%, transparent 100%);
	}
}

.testimonial {
	@include bp($point_4, min) {
		width: 48rem !important;
	}
	padding: 7.4rem 7rem;

	color: $black_1;


	transform-origin: 50% 50%;
	transform: scale(.85); 
	transition: all .3s;
	will-change: transform;

	.slick-current & {
		background-color: $accent_color;
		color: $white;
		transform: scale(1) !important;
		box-shadow: 0 26px 62px rgba(64, 129, 124, 0.36);
	}

	@include bp($point_4 - 1) {
		width: 100%;
		padding: 4rem 6rem;
	}
}

.testimonial_text {
	margin-bottom: 5.6rem;

	font-size: 2.2rem;
	line-height: 1.65;
	font-weight: 500;

	@include bp($point_4 - 1) {
		margin-bottom: 2rem;

		font-size: 1.9rem;
		line-height: 1.45;
	}
}

.testimonial_author {
	@extend %global_flex_block_row_nowrap_flex-start;
}

.testimonial_avatar {
	position: relative;

	width: 5rem;
	height: 5rem;
	margin-right: 2rem;
	flex-shrink: 0;
	border-radius: 50%;

	&:after {
		content: '';
		position: absolute;
		top: -4px;
		right: -4px;
		bottom: -4px;
		left: -4px;

		border: 2px solid rgba(255, 255, 255, 0.5);
		border-radius: 50%;
	}
}

.testimonial_avatar_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: center;
}

.testimonial_name {
	margin-bottom: .6rem;

	font-size: 1.8rem;
	font-weight: 700;
}

.testimonial_job {
	font-size: 1.5rem;
	font-weight: 500;
	opacity: .7;
}

.slider_control {
	@extend %global_flex_block_row_nowrap_center_center;

	position: absolute;
	top: 50%;
	left: 50%;

	width: 4.4rem;
	height: 4.4rem;
	margin-top: -2.2rem;

	background-color: $white;
	box-shadow: 0 4px 10px rgba(35, 62, 60, 0.3);
	border-radius: 50%;

	z-index: 5;
	cursor: pointer;

	&.prev_mod {
		margin-left: -42rem;

		@include bp($point_3) {
			margin-left: -30rem;
		}

		@include bp($point_4 - 1) {
			margin-left: 0;
			left: 1rem;
		}
	}

	&.next_mod {
		left: auto;
		right: 50%;
		margin-right: -42rem;

		@include bp($point_3) {
			margin-right: -30rem;
		}

		@include bp($point_4 - 1) {
			margin-right: 0;
			right: 1rem;
		}
	}

	&:hover, &:focus {
		color: $accent_color;
	}

	@include bp($point_4 - 1) {
		width: 3rem;
		height: 3rem;
	}
}